cc.Class({
    extends: cc.Component,
    //
    properties: {
        userID: 20,
        userName: "Foobar",
        // JavaScript 
        height: 20, // number
        type: "actor", // string
        loaded: false, // boolean                
        target: null, // object
        //cc.Nodecc.Vec2 
        target: cc.Node,
        pos: cc.Vec2,
        // cc.ValueType cc.Vec2cc.Color  cc.Rect
        pos: new cc.Vec2(10, 20),
        color: new cc.Color(255, 255, 255, 128),
        //
        any: [], // 
        bools: [cc.Boolean],
        strings: [cc.String],
        floats: [cc.Float],
        ints: [cc.Integer],

        values: [cc.Vec2],
        nodes: [cc.Node],
        frames: [cc.SpriteFrame],
        //

        //  
        score: {
            default: 0,
            displayName: "Score (player)",
            tooltip: "The score of player",
        },
        // get  set  get  set 
        width: {
            get: function() {
                return this._width;
            },
            set: function(value) {
                this._width = value;
            }
        }
        // foo: {
        //     // ATTRIBUTES:
        //     default: null,        // The default value will be used only when the component attaching
        //                           // to a node for the first time
        //     type: cc.SpriteFrame, // optional, default is typeof default
        //     serializable: true,   // optional, default is true
        // },
        // bar: {
        //     get () {
        //         return this._bar;
        //     },
        //     set (value) {
        //         this._bar = value;
        //     }
        // },
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},|
    //
    start() {
        // var node = this.node;
        // node.x = 100;

        var label = this.getComponent(cc.Label);
        var text = this.String + '111';

        // Change the text in Label Component
        label.string = text;
    },

    // update (dt) {},
});